import Login from "./Login"
import { get,post } from 'superagent';
import { useEffect, useState } from "react";
import NeGateWays from "./NeGateWays";

function MainTab() {


    /*const [SessionDetails, setSessionDetails] = useState({
        host: "",
        port: "",
        sessionId:""
    });*/
    
    const [SessionId, setSessionId] = useState("");
    const [Host, setHost] = useState("");
    const [Port, setPort] = useState("");

    // if (SessionId === "") {
    //     return (
    //         <>
    //             <Login doLogin={doLogin} />
    //         </>
    //     );
    // }
    // else {
    //     console.log("SessionId :: ",SessionId);
    //     return (
    //             <>
    //                 <p> NeGateWays Page</p>
    //                 <NeGateWays host={Host} port={Port} sessionid={SessionId}/>
    //             </>
    //     );
    // }

    useEffect(() => {
        console.log("Session ID")
    }, [])

    return (
        <>
            {SessionId === "" ? (<Login doLogin={doLogin} />) : (<><p>NeGateWays Page</p>
            <NeGateWays host={Host} port={Port} sessionid={SessionId}/></>)}
        </>
        
    )

    function doLogin(host, port, username, password) {

        const URL = "http://" + host + ":" + port;
        const credentials = {
            "userName": username,
            "plainPassword": password,
            "host": host,
            "language": "en",
            "mgmtClientType": 4,
            "securityQuestion": "",
            "securityAnswer": "",
            "newPlainPassword": "",
            "callbackURL":""
        }


        //post method with headers
        post(URL+'/login')   //URI
            .send(credentials)   //JSON object to send
            .set({ "Content-Type": "application/json" }) //Headers
            .end((err, res) => {
                setHost(host);
                setPort(port);
                setSessionId(res.body[0]);
            });
    }

}

export default MainTab;